/* cart */




.cart {
    width:60%;
    left:20%;
    top:22%;
    height:50%;
    z-index: 1005;
    background: linear-gradient(rgb(99, 162, 192), rgb(62, 62, 204));
    color:rgb(243, 235, 235);
    text-shadow: 0px 1px 1px  black;
    opacity: 0.9;
    overflow-y: scroll;
    border-radius:5px;
    display: none;
  }

  
  .cart .cartProduct {
      border:1px solid black;
      padding:20px;
      font-size:17px;
      display: -webkit-inline-flex;
      display: inline-flex;
  }
  .cartProduct div {
      margin:5px;
  }
  .cartIMG {
      width:30%;
      min-height:100px;
      background-size: 100% 100%;
      background-position: center;
      font-size:20px;
      
  }
  .cart .forAll {
      position: relative;
      margin-top: 15px;
      text-align: center;
      width:150px;
      margin-left:40%;  
  }
  .cart .goOrder {
    position: relative;
    width:150px;
    height:40px;
    margin-top:10px;
    margin-left:40%; 
    cursor: pointer;
    border-radius:10px;
    transition: ease-in-out 0.3s all;
  }
  .cart .goOrder:hover {
      opacity: 0.9;
      border-radius: 20px;
      background:rgb(216, 211, 211);
  }
  .cart .close {
      width:30px;
      height:30px;
      border-radius: 50%;
      cursor: pointer;
  }
  .cart .check {
      border-radius:5px;
      font-size: 25px;
      padding: 5px 10px;
      margin-left:45%;
      margin-top:10px;
      font-family: Arial, Helvetica, sans-serif;
      cursor: pointer;
      transition:0.5s ease-in all;
      
  }
  .check:hover {
    background: radial-gradient(white, rgb(138, 132, 132));
    border-radius: 20px;
    transition: 0.2s all ease-in-out;
  }
  .exitCart {
      position: fixed;
      font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
      
  }
  .exitCart:hover {
      border-bottom-right-radius: 30%;
      transition: 0.1s all ease-in-out;
      background: radial-gradient(white,gray);
      font-weight:600;
      cursor: pointer;
      transition:0.5s ease-in all;
  }
  .zaslonaCart {
      width:100%;
      height:100%;
      left:0%;
      top:0%;
      z-index: 1003;
      background:radial-gradient(white,gray);
      opacity: 0.9;
      position: fixed;
      display: none;

  }

  /*Ilościówki */
  .cartQuanityDiv {
     display: -webkit-inline-flex;
     display: inline-flex;
     margin:5px;
  }
  .cartQuanityDiv div {
      font-size: 20px;
  }
.cartQuanityDiv :first-child, .cartQuanityDiv :last-child{
    cursor:pointer;
    height:20px;
    margin-top:10px;
}
.cartQuanityDiv :first-child:hover, .cartQuanityDiv :last-child:hover{
    color: rgb(207, 202, 202);
}



